<template>
  <div class="logo">
    <img v-if="imageUrl" class="logo-img" :src="imageUrl" :alt="altText" />
    <span v-if="!hideText" :class="size">{{ brandName }}</span>
  </div>
</template>
<script setup lang="ts">
import { computed } from 'vue';
const props = defineProps<{
  imageUrl?: string;
  brandName?: string;

  hideText?: boolean;
  size?: 'small' | 'medium' | 'large';
}>();

const logoHeight = computed(() => {
  if (props.size === 'small') return '24px';
  if (props.size === 'large') return '80px';
  return '50px';
});
const altText = computed(() => `${props.brandName} Logo`);
</script>
<style lang="scss" scoped>
.logo {
  display: flex;
  gap: 5px;
  align-items: center;

  .logo-img {
    height: v-bind(logoHeight);
    max-width: 100%;
  }

  span {
    &.small {
      font-size: 12px;
      font-size: 14px;
      font-weight: 600;
    }
    &.medium {
      font-size: 14px;
      font-size: 16px;
      font-weight: 600;
    }
    &.large {
      font-size: 16px;
      font-size: 18px;
      font-weight: 600;
    }
  }
}
</style>
